@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Poppins:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}
.container {
  width: 1100px;
  margin: auto;
}
html,
body {
  background: #e3e0cf;
}

.header {
  display: flex;
  margin: auto;
  justify-content: space-between;
  padding: 30px;
  position: relative;
  -webkit-animation-name: search;
          animation-name: search;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
.header > .logo {
  display: inline-block;
  position: relative;
  width: 100%;
}
.header > .logo > img {
  position: absolute;
  left: 25%;

  display: flex;
  width: 284px;
  height: 177px;
}
.font-awesome {
  flex-basis: 19%;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  height: 177px;
  width: 200px;
}
.icon {
  cursor: pointer;
  margin-bottom: 10px;
}
.font-awesome > h1 {
  color: white;
  size: 40px;
  text-shadow: 2px 2px #888888;
}

.search {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  -webkit-animation-name: search;
          animation-name: search;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes search {
  0% {
    top: -300px;
  }
  100% {
    top: 0px;
  }
}

@keyframes search {
  0% {
    top: -300px;
  }
  100% {
    top: 0px;
  }
}

.search-form > .form-control {
  height: 80px;
  width: 500px;
  border-radius: 40px;
  direction: none;
  border: none;
  text-align: center;
  border: 3px solid #033f01;
  background-color: #ece5dd;
  font-size: 40px;
}
.search-form > .form-control:focus {
  outline: none;
}

.social-links > a > .fab {
  height: 70px;
  width: 70px;
  padding-top: 15px;
  font-size: 30px;
  line-height: 40px;
  border: 1px solid #e3e0cf;
  margin: 40px 5px 0;
  color: #e3e0cf;
  cursor: pointer;
  transition: 1s;
}
.social-links > a {
  margin-left: 25px;
}
.social-links > a > .fab:hover {
  background: #e3e0cf;
  color: #222222;
  -webkit-transform: translateY(-7px);
          transform: translateY(-7px);
}

.social-links > p {
  font-size: 25px;
  padding-top: 20px;
  margin-top: 40px;
}
@-webkit-keyframes social {
  0% {
    top: 500px;
  }
  100% {
    top: 0px;
  }
}
@keyframes social {
  0% {
    top: 500px;
  }
  100% {
    top: 0px;
  }
}

.social-links {
  color: #fff;
  background-color: #222222;
  text-align: center;
  position: relative;
  -webkit-animation-name: social;
          animation-name: social;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.character-detail {
  height: 100%;

  display: flex;
  overflow: hidden;
  justify-content: center;
  position: relative;
  transition: 1s;
  -webkit-animation-name: myanimation;
          animation-name: myanimation;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
@-webkit-keyframes myanimation {
  0% {
    left: -1300px;
  }
  100% {
    left: 0px;
  }
}
@keyframes myanimation {
  0% {
    left: -1300px;
  }
  100% {
    left: 0px;
  }
}

.character-img {
  width: 300px;
  height: 450px;

  align-items: center;
  border-radius: 20px;
  object-fit: cover;
}

.property {
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 5px;
}

.over-strong {
  text-align: center;
  flex: 30% 1;
}
.over-p {
  text-align: center;
  flex: 70% 1;
  font-size: 14px;
}

.description {
  width: 90%;
  margin: auto;
  position: absolute;
  top: 0%;
  overflow: hidden;
  justify-content: center;
  display: flex;
  flex-direction: column;
  color: azure;
  opacity: 0;
  transition: top 1s;
}

.character-detail:hover > .description {
  top: 30%;

  cursor: pointer;

  opacity: 1;
}
.overlay {
  width: 300px;
  height: 450px;
  position: absolute;
  top: 0;
  left: 16.665px;
  border-radius: 20px;
  cursor: pointer;
  background: linear-gradient(#369457, #032202);
  opacity: 0;
  transition: 1s;
}
.character-detail:hover {
  border-radius: 30%;
  overflow: hidden;
}

.character-detail:hover > .overlay {
  opacity: 0.7;
}

.character-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: auto;
  grid-row-gap: 35px;
  row-gap: 35px;
  justify-content: space-around;
  width: 1000px;
}

.img-of-item {
  display: flex;
  justify-content: center;
}
.character-details {
  display: flex;
  flex-direction: column;
  color: #333333;
  size: 40px;
  position: relative;
  text-shadow: 5px 5px 10px #888888;
  -webkit-animation-name: character-details;
          animation-name: character-details;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  margin-bottom: 40px;
}
@-webkit-keyframes character-details {
  0% {
    left: -1000px;
  }
  100% {
    left: 0px;
  }
}
@keyframes character-details {
  0% {
    left: -1000px;
  }
  100% {
    left: 0px;
  }
}

.name-of-item {
  margin: auto;
  margin-bottom: 30px;
  font-size: 40px;
}
.item-img {
  width: 375px;
  height: 500px;

  margin-bottom: 30px;
  margin-top: 30px;
  border-radius: 20px;
  object-fit: cover;
}

.detail {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.describe,
.perticular {
  flex-basis: 50%;
  text-align: center;
}
.quotes {
  display: block;
  flex-basis: 100%;
  font-size: 35px;
  text-align: center;
}
.perticular {
  font-size: 35px;
}

.describe {
  font-size: 25px;
}
.describe > span {
  margin-left: 20px;
}

.quote {
  font-size: 25px;
  text-align: center;
  display: block;
}

.quote > .a {
  margin: auto;
  margin-top: 30px;
}

nav {
  width: 100%;
  height: 60px;
  margin-top: 30px;
  margin-bottom: 40px;
  color: black;
}
.pagination {
  list-style-type: none;
  justify-content: center;
  display: flex;
}

.page-item {
  position: relative;
  height: 60px;
  width: 60px;

  border: 1px solid #032202;
}
.page-link {
  display: block;
  font-weight: bold;
  font-size: x-large;
  margin-top: 10px;
}
.page-item {
  cursor: pointer;
  text-align: center;
}

.page-item:hover {
  background-color: #d3a409;
  color: white;
}

